<template>
  <div class="collectList">
    <div class="search">
      <form action="/">
       <van-search v-model="value" show-action action-text="" @cancel="onSearch" placeholder="" background="#fff" @search="onSearch" @blur="blur" />
      </form>
    </div>
    <div class="mid mescroll">
      <div class="list" id="list">
        <div id="listData">
          <div class="item" v-for="(item,index) in list" :key="index" @click="goDetail(item.learnCode)">
            <van-swipe-cell>
              <div class="flex">
                <div class="left">
                  <img :src="item.aimg?$utils.dealImg(item.aimg):require('@/assets/img/default/defaultH.png')" alt />
                </div>
                <div class="right">
                  <p class="title">{{item.learnName}}</p>
                  <div class="bottom">
                    <div class="text1">{{item.aoutPrice?`${item.asaleNum}`:`${item.avisitNum}`}}</div>
                    <div class="text2">{{item.aoutPrice?''+item.aoutPrice.toFixed(2):''}}</div>
                  </div>
                </div>
              </div>
              <template slot="right">
                <van-button square type="danger" text="" @click="del(item)" />
              </template>
            </van-swipe-cell>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { SwipeCell, Search, Button } from "vant";
import { getCollectList, delCollect } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [SwipeCell.name]: SwipeCell,
    [Button.name]: Button,
    [Search.name]: Search
  },
  data() {
    return {
      active: 0,
      value: "",
      list: []
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    onSearch() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    onClick() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    goDetail(acode) {
      sessionStorage.setItem("acode", acode);
      this.$router.push({
        name: "courseDetail"
      });
    },
    del(item) {
      const loading = this.$toast.loading({
        duration: 0, //  toast
        forbidClick: true // 
      });
      delCollect({
        learnCode: item.learnCode
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.mescroll.resetUpScroll();
        } else {
          $this.$toast.fail(res.errorMsg);
        }
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        learnName: this.value
      };
      getCollectList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //vuemountedmescroll,id
        up: {
          callback: this.upCallback, //
          //,
          isBounce: false, //ios,(,): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //8,10
          empty: {
            //
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: ""
            //						  	btntext : " >" ,
            //						  	btnClick : function() {
            //						  		alert("");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">--  --</p>',
          lazyLoad: {
            use: true // ,false
          }
        }
      });
    },
    // page = {num:1, size:10}; num: ,1; size:,10
    upCallback: function(page) {
      //
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //,empty

          // (clearIdclearEmptyId)
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //,;
          //mescroll,,;,;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //():  totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //(, )
        },
        function() {
          //,;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.collectList {
  .mid {
    background: #fff;
    height: calc(100vh - 54px);
    .list {
      padding: 10px;
      .item {
        margin-bottom: 10px;
        border-bottom: 1pt solid #efefef;
        padding: 5px 0;
        background: #fff;
        .flex {
          display: flex;
          .left {
            img {
              width: 120px;
              margin-right: 5px;
            }
          }
          .right {
            position: relative;
            flex: 2;
            margin-right: 5px;
            .title {
              font-size: 13px;
              line-height: 20px;
              max-height: 50px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            .bottom {
              position: absolute;
              bottom: 0;
              left: 0;
              display: flex;
              width: 100%;
              line-height: 30px;
              .text1 {
                color: #999;
                font-size: 13px;
                flex: 2;
              }
              .text2 {
                flex: 1;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
        }
      }
    }
  }
}
</style>
<style lang="scss">
.collectList {
  .van-button {
    height: 100%;
  }
}
</style>
